#snackbar {
    visibility: hidden;
    min-width: 250px;
    margin-left: -125px;
    background-color: #333;
    color: #fff;
    text-align: center;
    border-radius: 12px;
    padding: 16px;
    position: fixed;
    z-index: 1;
    left: 50%;
    top: 30px;
    font-size: 17px;
  }

#snackbar.show {
    visibility: visible;
    animation: fadein .5s, fadeout .5s 2.5s;
}

@keyframes fadein {
    from{top: 0; opacity: 0;}
    to{top: 30px; opacity: 1;}
}

@keyframes fadeout {
    from{top: 30px; opacity: 1;}
    to{top: 0px; opacity: 0;}
}